@import "@/assets/scss/config/_colors.scss";














































.viewport {
  min-height: 100vh;
  overflow-x: hidden;
}
#nav {
  position: sticky;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
#nav .router-link-exact-active{
  background-color: #198dae;
}
.fa-layers{
  position:relative;
}
.fa-layers-counter{
  position: absolute;
    top: -.5em;
    right: -1em;
    border-color: #00386b;
}
.router-link-exact-active .fa-layers-counter{
    border-color: #198dae;
}
